import Vue from "vue";
import VueRouter from "vue-router";
import fade from "../views/fade.vue";
import slide from "../views/slide.vue";
import zoom from "../views/zoom.vue";

Vue.use(VueRouter);

const routes = [
  { path: "/", redirect: "/fade" },
  {
    path: "/fade",
    name: "fade",

    meta: { index: 0 },
    component: fade,
  },
  {
    path: "/slide",
    name: "slide",
    meta: { index: 1 },
    component: slide,
  },
  {
    path: "/zoom",
    name: "zoom",
    meta: { index: 2 },
    component: zoom,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
